import { createReducer } from "@reduxjs/toolkit";
import { addUsers, getUsers, addInfo, getInfo } from "./userActions";

let initialState = [];

const userReducer = createReducer(initialState, {
  [addUsers]: (state, action) => [...action.payload],
  [getUsers]: (state, action) => [...state],
});

const infoReduser = createReducer(initialState, {
  [addInfo]: (state, action) => [...action.payload],
  [getInfo]: (state, action) => [...state],
});

export { userReducer, infoReduser };
