import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "../assets/scss/App.scss";
import { getUserInfo } from "./Service";
import { connect } from "react-redux";
import { addInfo } from "../store/userActions";

function UserListItem({ user, addInfo }) {
  const [userName, setUserName] = useState("No Name");
  const [repoCounter, setRepoCounter] = useState("0");

  useEffect(() => {
    getInfo(user.login);
  }, [user]);

  async function getInfo(login) {
    let userInfo = await getUserInfo(login);
    setUserName(userInfo.name);
    setRepoCounter(userInfo.public_repos);
  }
  async function hendleOnClick() {
    let repoInfo = await getUserInfo(`${user.login}/repos`);
    let userInfo = await getUserInfo(user.login);
    addInfo([userInfo, repoInfo]);
  }

  return (
    <li className="userListItem">
      <img src={user.avatar_url} alt="User avatar" className="userAvatar" />
      <h3>
        <Link to={`/:${user.login}`} onClick={hendleOnClick}>
          {userName}
        </Link>
      </h3>
      <h3>Repo: {repoCounter}</h3>
    </li>
  );
}

const mapStateToProps = (state) => {
  return {};
};

const mapDispatchToProps = {
  addInfo,
};

export default connect(mapStateToProps, mapDispatchToProps)(UserListItem);
