import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import "../assets/scss/App.scss";

function UserInfo({ userInfo }) {
  const [user, setUser] = useState({});
  const [repo, setRepo] = useState([]);
  const [filteredRepo, setFilteredRepo] = useState([]);

  useEffect(() => {
    if (userInfo.length !== 0) {
      setUser(userInfo[0]);
      setRepo(userInfo[1]);
      setFilteredRepo(userInfo[1]);
    }
  }, [userInfo]);

  function handleOnChange(e) {
    e.preventDefault();
    setFilteredRepo(repo.filter((el) => el.name.includes(e.target.value)));
  }

  return (
    <div className="app">
      <h1>GitHub Searcher</h1>
      <div>
        <div className="userInfoContainer">
          <img
            src={user.avatar_url ? user.avatar_url : ""}
            alt="User Foto"
            className="userInfoAvatar"
          />
          <div>
            <ul className="userInfoList">
              <li>{user.name ? user.name : "no name"}</li>
              <li>{user.email ? user.email : "hide"}</li>
              <li>{user.location ? user.location : "without location"}</li>
              <li>{user.created_at ? user.created_at : "is created"}</li>
              <li>{user.followers ? user.followers : 0} Followers</li>
              <li>Following {user.following ? user.following : 0}</li>
            </ul>
          </div>
        </div>
        <p className="userInfoBio">{user.bio ? user.bio : "no biography"}</p>
      </div>
      <input type="text" onChange={handleOnChange} />
      <ul className="userRepoList">
        {filteredRepo.map((el) => (
          <li key={el.id} className="userListItem">
            <h3>
              <a href={el.html_url} target="blank">
                {el.name}
              </a>
            </h3>
            <div className="repoInfo">
              <h4>{el.forks} Forks</h4>
              <h4>{el.stargazers_count} Stars</h4>
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    userInfo: state.infoReduser,
  };
};

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(UserInfo);
